/* Modal container */
.modal {
    position: fixed; 
    z-index: 1; 
    left: 0;
    top: 0;
    width: 100%; 
    height: 100%; 
    overflow: hidden; 
    background-color: rgba(0,0,0,0.4); 
}

/* Modal content */
.modal-content {
    background-color: #fefefe;
    margin: 10% auto; 
    padding: 20px;
    border: 1px solid #888;
    width: 80%; 
    box-shadow: 0 5px 15px rgba(0,0,0,0.3);
    animation: modalopen 0.4s;
}

.modal-header {
    padding: 10px 0;
    border-bottom: 1px solid #888;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.modal-body {    
    padding: 20px 0;    
    max-height: 50vh;
    overflow-y: auto;
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 2em;
}

.modal-footer {
    padding: 10px 0;
    border-top: 1px solid #888;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}


.modal-close {
    color: #aaa;
    float: right;
    font-size: 28px;
    font-weight: bold;
    cursor: pointer;
}

/* Animation */
@keyframes modalopen {
    from {opacity: 0}
    to {opacity: 1}
}