.inventory-table {
  width: 100%;
  border-collapse: collapse;
  margin: 20px 0;
  font-size: 18px;
  text-align: left;
}

.table-header,
.table-row {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
}

.inventory-table .table-cell {
  padding: 12px 15px;
  border: 1px solid #ddd;
}

.inventory-table .table-header {
    background-color: #00000040;  
  font-weight: bold;
}

.inventory-table .table-row:nth-child(even) {
    background-color: #ffffff70;  
}

.inventory-table .table-row:hover {
  background-color: #ffffffc0;  
}

.inventory-table .action-buttons {
  display: flex;
  gap: 10px;
}

.inventory-table .action-buttons button {
  padding: 5px 10px;
  border: none;
  cursor: pointer;
}