.connect-wrapper {
    display: "flex"; 
    gap: "1em"; 
    justify-content: "center"; 
    align-items: "center";
}

.connect-row {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
    margin-bottom: 10px;
}

.connect-row svg {
    height: 40px;
    width: 40px;
    opacity: .6;
    filter: drop-shadow(0px 0px 3px #00000040);
}